<template>
    <el-dialog title="" :visible.sync="dialogVisible" width="340px" >
        <el-form ref="form" :model="form">
            <el-form-item label="">
                <el-date-picker type="date" placeholder="" v-model="form.date"></el-date-picker>
            </el-form-item>
            <el-form-item label="">
                <el-input  type="textarea" :autosize="{ minRows: 2, maxRows: 4}" placeholder=""
                    v-model="form.content">
                </el-input>
            </el-form-item>
            <el-form-item>
                <el-col class="mt-12">
                    <el-button type="primary" class="w-full" @click="onSubmit" :loading="loading"></el-button>
                </el-col>
            </el-form-item>
        </el-form>
    </el-dialog>
</template>

<script>
import { quillEditor } from 'vue-quill-editor'
import { commonUtils } from '@/utils/common'
import reportApi from '@/api/report'
export default {
    components: {
        quillEditor
    },
    props:{
        editInfo:{
            type:Object,
            default:()=>{
                return null
            }
        },
        mode:{
            type:String,
            default:'add'
        }
    },
    data() {
        return {
            form: {
                date: new Date(),
                content: '',
                id:'',
                week:''
            },
            editorOption: {
                modules: {
                    toolbar: [
                        ['bold', 'italic', 'underline', 'strike'], //    
                        // ['blockquote', 'code-block'], //   
                        [{ header: 1 }, { header: 2 }], // 12 
                        [{ list: 'ordered' }, { list: 'bullet' }], // 
                        // [{ script: 'sub' }, { script: 'super' }], // /
                        // [{ indent: '-1' }, { indent: '+1' }], // 
                        // [{ direction: 'rtl' }], // 
                        [{ size: ['12', '14', '16', '18', '20', '22', '24', '28', '32', '36'] }], // 
                        [{ header: [1, 2, 3, 4, 5, 6] }], // 
                        [{ color: [] }, { background: [] }], // 
                        [{ font: ['songti'] }], // 
                        // [{ align: [] }], // 
                        ['clean'], // 
                        // ['image', 'video'] // 
                    ]
                },
                placeholder: ''
            },
            dialogVisible: false,
            loading :false
        }
    },

    computed: {

    },
    watch:{
        editInfo:{
            immediate:true,
            handler(val){
                if(val){
                    this.form.date =commonUtils.parseTime(parseInt(val.date),'{y}-{m}-{d}')
                    this.form.content = val.content
                    this.form.id = val.id
                    this.form.week =  val.week
                }else{
                    this.form.date =new Date()
                    this.form.content = ''
                    this.form.id = ''
                    this.form.week = ''
                }
                
            }
        }
       
    },
    mounted() {
    },
    methods: {
        open() {
            this.dialogVisible = true
        },
        onSubmit() {
            this.loading = true
            var weekArr = new Array("", "", "", "", "", "", "");  
            this.form.week = weekArr[new Date(this.form.date).getDay()]
            reportApi.updateReport(this.form).then(res => {
                this.loading = false
                if (res.data.resultCode === '01') {
                    this.$message({
                        message: '',
                        type: 'success'
                    });
                    this.$store.dispatch('updateReportList')
                   this.dialogVisible = false
                }else{
                    this.$message.error('');
                }
            }).catch(err=>{
                this.loading = false
                this.$message.error(err);
              
            })
        }
    },
}
</script>
<style lang='scss'>

</style>